.input2 /deep/ .el-form-item__error {
  margin: 240px 0 0 130px;
  font-size: 20px;
}

.cake {
  animation: move 2s 0s 50 forwards;
  -webkit-animation: move 2s 0s 50 forwards;
  transform-origin: bottom;
  -webkit-transform-origin: bottom;
}

@keyframes move {
  0%, 65% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  70% {
    -webkit-transform: rotate(6deg);
    transform: rotate(6deg);
  }
  75% {
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
  }
  80% {
    -webkit-transform: rotate(6deg);
    transform: rotate(6deg);
  }
  85% {
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
  }
  90% {
    -webkit-transform: rotate(6deg);
    transform: rotate(6deg);
  }
  95% {
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@-webkit-keyframes move {
  0%, 65% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  70% {
    -webkit-transform: rotate(6deg);
    transform: rotate(6deg);
  }
  75% {
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
  }
  80% {
    -webkit-transform: rotate(6deg);
    transform: rotate(6deg);
  }
  85% {
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
  }
  90% {
    -webkit-transform: rotate(6deg);
    transform: rotate(6deg);
  }
  95% {
    -webkit-transform: rotate(-6deg);
    transform: rotate(-6deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

.new-item3 /deep/ .el-carousel__button {
  margin-left: -100px;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #d2d7dc;
  margin-top: 10px;
}

.new-item4 /deep/ .is-active .el-carousel__button {
  background: red;
  margin-left: -100px;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  margin-top: 10px;
}

.banner /deep/ .el-carousel__indicator .el-carousel__indicator--horizontal{
  position: absolute;
  bottom: 0;
  left: 0;
}
</style>




